.editor-styles-wrapper > .is-root-container {
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.editor-styles-wrapper {
	background-color: #c2c2c2 !important;
    padding: 0 !important;

    h2 {
        font-family: 'DIN-Bold', sans-serif !important;
        font-size: clamp(18px, 8vw, 128px) !important;
        color: #fff;
        line-height: 1 !important;
        text-transform: uppercase;
        max-width: 100% !important;
        margin: 0 !important;
    }

    p {
        margin: 20px 0 0 0 !important;
        max-width: 100%;
    }

    img {
        height: 100% !important;
    }
}

.block-editor-media-placeholder {
        max-width: 1248px;
}

.wp-block-block-test-hero-block {

    &__btn-poster--select {
        color: #fff;
        font-family: 'Karla', sans-serif;
	    background-color: #007cba;
	    transition: all 0.2s;

        &:hover {
            background-color: #006ba1;
            border-color: transparent;
            color: #fff;
            }
    }

    &__btn-poster--remove {
        color: #fff;
        font-family: 'Karla', sans-serif;
	    background-color: #007cba;
	    transition: all 0.2s;
        margin-top: 10px;

        &:hover {
            background-color: #006ba1;
            border-color: transparent;
            color: #fff;
            }
    }
}

.wp-block-block-test-hero-block
    > .wp-block-block-test-hero-block__inner-blocks
	> .block-editor-inner-blocks
	> .block-editor-block-list__layout
	> .wp-block-button {
        display: flex;
        justify-content: left;
        max-width: 100%;
        margin: 59px 0 0 0;
    }

.wp-block-block-test-hero-block
    > .wp-block-block-test-hero-block__inner-blocks
	> .block-editor-inner-blocks
	> .block-editor-block-list__layout
	> .wp-block-button
	> .wp-block-button__link {
	position: relative;
	display: inline-block;
	text-decoration: none;
	color: #333 !important;
    font-family: 'Karla', sans-serif;
	background-color: #00aeef !important;
	transition: all 0.2s;
	cursor: pointer;
	padding: 16px 48px 15px 48px;
	letter-spacing: 0.4px;
	font-size: 18px;
	line-height: 17px;
	border-radius: 30px;
	font-weight: 600;

    &:hover {
        background-color: rgba(92, 203, 245, 0.9) !important;
        border-color: transparent !important;
        }
}